
import { memo } from 'react';
import ListItem from '@material-ui/core/ListItem';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import { useMediaQuery } from '@material-ui/core';

import OutlinedButton from 'components/UI/Buttons/OutlinedButton';

const useStyles = makeStyles(theme => ({
  menuItem: {
    flexDirection: 'row',
    width: 'fit-content',
    minHeight: '100%',
    padding: 0,

  }
}));

const TopAppBarMenu = () => {
  const classes = useStyles();
  const theme = useTheme();
  const isMd = useMediaQuery(theme.breakpoints.up('md'), {
    defaultMatches: true,
  });

  const menuItemClickHandler = (url) => {
    window.open(url, '_blank');
  };

  return (
    <>
      <ListItem className={classes.menuItem}>
        {/* {isMd &&
          <OutlinedButton onClick={() => menuItemClickHandler('https://safe-mars.gitbook.io/safemars/')} style={{ border: 'none' }}>
            <Typography variant='body2' style={{ fontFamily: 'LULO' }}>ANNOUNCEMENTS</Typography>
          </OutlinedButton>} */}
        <OutlinedButton onClick={() => menuItemClickHandler('https://safe-mars.gitbook.io/safemars/')} style={{ border: 'none' }}>
          <Typography variant='body2' style={{ fontFamily: 'LULO' }}>ABOUT</Typography>
        </OutlinedButton>
        {isMd &&
          <OutlinedButton onClick={() => menuItemClickHandler('https://safe-mars.gitbook.io/safemars/')} style={{ border: 'none' }}>
            <Typography variant='body2' style={{ fontFamily: 'LULO' }}>DOCS</Typography>
          </OutlinedButton>}
      </ListItem>
    </>
  );
};

export default memo(TopAppBarMenu);
